<template>
    <view class="integral_devidend_info_page">
        <!-- tabbar -->
        <cu-custom><block slot="content">分红金详情</block></cu-custom>
       <!-- 头部 -->
        <view class="things_width_full flex justify-start align-start integral_devidend_info_page_top">
            <!-- 余额 -->
            <view class="flex justify-center align-start balance">
                <view class="balance_text">分红金 (元)</view>
                <view class="balance_value">{{userAmount.profit_points}}</view>
            </view>
            <!-- 积分规则 -->
            <view class="rule_info">
                <view class="rule_text">积分规则</view>
                <view class="flex justify-end align-center btn_box">
                    <button class="cu-btn">提现</button>
                </view>
            </view>
        </view>
        <!-- 列表 -->
        <view class="devidend_info_list">
            <view class="flex justify-center align-center devidend_info_list_title">积分明细</view>
            <scroll-view v-if="dividendList.length > 0" class="devidend_list" scroll-y>
                <view class="flex justify-between align-center devidend_item">
                    <view class="devidend_item_left">
                        <view class="devidend_item_from">转化</view>
                        <view class="devidend_item_time">2020.12.12  12:00</view>
                    </view>
                    <view class="devidend_item_right">+1245</view>
                </view>
                <view class="flex justify-between align-center devidend_item">
                    <view class="devidend_item_left">
                        <view class="devidend_item_from">转化</view>
                        <view class="devidend_item_time">2020.12.12  12:00</view>
                    </view>
                    <view class="devidend_item_right">+1245</view>
                </view>
                <view class="flex justify-between align-center devidend_item">
                    <view class="devidend_item_left">
                        <view class="devidend_item_from">转化</view>
                        <view class="devidend_item_time">2020.12.12  12:00</view>
                    </view>
                    <view class="devidend_item_right devidend_item_right_error">-1245</view>
                </view>
                <view class="flex justify-between align-center devidend_item">
                    <view class="devidend_item_left">
                        <view class="devidend_item_from">转化</view>
                        <view class="devidend_item_time">2020.12.12  12:00</view>
                    </view>
                    <view class="devidend_item_right">+1245</view>
                </view>
                <view class="flex justify-between align-center devidend_item">
                    <view class="devidend_item_left">
                        <view class="devidend_item_from">转化</view>
                        <view class="devidend_item_time">2020.12.12  12:00</view>
                    </view>
                    <view class="devidend_item_right devidend_item_right_error">-1245</view>
                </view>
                <view class="flex justify-between align-center devidend_item">
                    <view class="devidend_item_left">
                        <view class="devidend_item_from">转化</view>
                        <view class="devidend_item_time">2020.12.12  12:00</view>
                    </view>
                    <view class="devidend_item_right devidend_item_right_error">-1245</view>
                </view>
                <view class="flex justify-between align-center devidend_item">
                    <view class="devidend_item_left">
                        <view class="devidend_item_from">转化</view>
                        <view class="devidend_item_time">2020.12.12  12:00</view>
                    </view>
                    <view class="devidend_item_right">+1245</view>
                </view>
                <view class="flex justify-between align-center devidend_item">
                    <view class="devidend_item_left">
                        <view class="devidend_item_from">转化</view>
                        <view class="devidend_item_time">2020.12.12  12:00</view>
                    </view>
                    <view class="devidend_item_right devidend_item_right_error">-1245</view>
                </view>
                <view class="flex justify-center align-center not_more">暂无更多</view>
            </scroll-view>
            <view v-else class="flex justify-center align-center kong_data_box">
                <image class="kong_image" src="./../../../../../static/images/global/kong.png" mode="widthFix" />
                <view class="kong_text">暂无数据</view>
            </view>
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>
<script>
import { giftsPointsList_api } from '@/api/index.js';
import { appData } from '@/utils/config.js';
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
import storage from '@/utils/storage.js';
import { pointsType_format } from '@/utils/filters.js';
let that = null;
export default {
    data() {
        return {
            userData: storage.get("userData") || {},
            userAmount: {},// 积分分数
            // 列表数据
            page: 1,// 页数
            page_size: 10,// 每页个数
            pageMore: true,// 列表状态，false:无更多数据,true:有更多数据
            dividendList: []
        }
    },
    watch: {
        '$store.getters.user_userData': function(val) {
            that.userData = val;
        },
    },
    filters: {
        pointsType_format
    },
    methods: {
        ...mapMutations([
            'modal',
            'user'
        ]),
        /**清空数据
         * @description: 
         * @param {*} e
         * @return {*}
         */
        clearPageFun(e) {
            that.page = 1;
            that.pageMore = true;
            that.profitPointsList = [];
            that.getFraction();
            that.getDividendList();
        },
        /**获取分红金列表
         * @description: 添加数据、判断是否有更多数据
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        getDividendList() {
            let params = {
                page: that.page,
                page_size: that.page_size,
                type: 20,
            };
            giftsPointsList_api(params,that).then(res=>{
                console.log(res);
                that.dividendList = [that.dividendList,...res.list];
                if (res.total > that.dividendList.length) {
                    that.pageMore = true;
                    that.page++;
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        /**获取更多数据
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        getMoreData(e) {
            if (that.pageMore) that.getDividendList();
        },
        /**获取积分分数
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getFraction(e) {
            let params = {};
            giftsPointsFraction_api(params,that).then(res=>{
                that.userAmount = res.userAmount;
            }).catch(err=>{
                console.log(err);
            })

        },
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
    },
    onShow: function() {
        that.clearPageFun();
    }
}
</script>

<style lang="scss" scoped>
    @import './index';
</style>
